import { NavLink } from "react-router-dom";

export default function Sidebar() {
  const links = [
    { name: "Dashboard", path: "/admin" },
    { name: "Add Employee", path: "/admin/add-employee" },
    { name: "Attendance", path: "/admin/attendance" },
    { name: "Payroll", path: "/admin/payroll" },
    { name: "Leave Requests", path: "/admin/leave-requests" },
  ];

  return (
    <aside className="bg-blue-800 text-white w-64 h-screen p-4 fixed">
      <h1 className="text-2xl font-bold mb-8">Admin Panel</h1>
      <nav className="space-y-4">
        {links.map((link) => (
          <NavLink
            key={link.name}
            to={link.path}
            className={({ isActive }) =>
              `block px-4 py-2 rounded hover:bg-blue-700 ${isActive ? "bg-blue-700" : ""}`
            }
          >
            {link.name}
          </NavLink>
        ))}
      </nav>
    </aside>
  );
} 