import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { toast } from 'react-toastify';
import '../styles/admin-dashboard.css';

const ApplicationsManagement = () => {
  const [applications, setApplications] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedApplication, setSelectedApplication] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [showHireModal, setShowHireModal] = useState(false);
  const [showAcceptModal, setShowAcceptModal] = useState(false);
  const [hireFormData, setHireFormData] = useState({
    salary: '',
    role: 'Worker'
  });
  const [hiringLoading, setHiringLoading] = useState(false);
  const [hiredInfo, setHiredInfo] = useState(null);

  // Fetch all job applications
  useEffect(() => {
    fetchApplications();
  }, []);

  const fetchApplications = async () => {
    try {
      setLoading(true);
      const token = localStorage.getItem('token');
      
      if (!token) {
        toast.error('Authentication error. Please login again.');
        return;
      }
      
      const response = await axios.get('http://localhost:5000/api/applications', {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
      
      setApplications(response.data);
    } catch (error) {
      console.error('Error fetching applications:', error);
      toast.error('Failed to load applications');
    } finally {
      setLoading(false);
    }
  };

  // Handle changing application status
  const handleStatusChange = async (applicationId, newStatus) => {
    try {
      const token = localStorage.getItem('token');
      
      if (!token) {
        toast.error('Authentication error. Please login again.');
        return;
      }
      
      await axios.put(`http://localhost:5000/api/applications/${applicationId}/status`, 
        { status: newStatus },
        {
          headers: {
            Authorization: `Bearer ${token}`
          }
        }
      );
      
      // Update the application in the local state
      setApplications(applications.map(app => 
        app._id === applicationId ? { ...app, status: newStatus } : app
      ));
      
      toast.success(`Application marked as ${newStatus}`);
    } catch (error) {
      console.error('Error updating application status:', error);
      toast.error('Failed to update application status');
    }
  };

  // Accept application and open hire modal
  const handleAccept = (application) => {
    setSelectedApplication(application);
    setHireFormData({
      salary: application.expectedSalary || '',
      role: 'Worker'
    });
    setHiredInfo(null);
    setShowAcceptModal(true);
  };

  // Reject application
  const handleReject = async (applicationId) => {
    if (window.confirm('Are you sure you want to reject this application?')) {
      await handleStatusChange(applicationId, 'Rejected');
    }
  };

  // View application details
  const viewApplicationDetails = (application) => {
    setSelectedApplication(application);
    setShowModal(true);
  };

  // Format date to readable format
  const formatDate = (dateString) => {
    const options = { year: 'numeric', month: 'short', day: 'numeric' };
    return new Date(dateString).toLocaleDateString(undefined, options);
  };

  // Download resume
  const downloadResume = async (applicationId) => {
    try {
      const token = localStorage.getItem('token');
      
      if (!token) {
        toast.error('Authentication error. Please login again.');
        return;
      }
      
      // This will trigger the browser to download the file
      window.open(`http://localhost:5000/api/applications/${applicationId}/resume`, '_blank');
    } catch (error) {
      console.error('Error downloading resume:', error);
      toast.error('Failed to download resume');
    }
  };

  // Download document
  const downloadDocument = async (applicationId, documentType) => {
    try {
      const token = localStorage.getItem('token');
      
      if (!token) {
        toast.error('Authentication error. Please login again.');
        return;
      }
      
      // This will trigger the browser to download the file
      window.open(`http://localhost:5000/api/applications/${applicationId}/document/${documentType}`, '_blank');
    } catch (error) {
      console.error(`Error downloading ${documentType}:`, error);
      toast.error(`Failed to download ${documentType}`);
    }
  };

  // Open hire modal
  const openHireModal = (application) => {
    setSelectedApplication(application);
    setHireFormData({
      salary: application.expectedSalary || '',
      role: 'Worker'
    });
    setHiredInfo(null);
    setShowHireModal(true);
  };

  // Handle form input change for hiring
  const handleHireFormChange = (e) => {
    const { name, value } = e.target;
    setHireFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  // Process hiring
  const handleHireSubmit = async (e) => {
    e.preventDefault();
    
    if (!hireFormData.salary || !hireFormData.role) {
      toast.error('Please fill all required fields');
      return;
    }
    
    try {
      setHiringLoading(true);
      const token = localStorage.getItem('token');
      
      if (!token) {
        toast.error('Authentication error. Please login again.');
        return;
      }
      
      const response = await axios.post(
        `http://localhost:5000/api/applications/${selectedApplication._id}/hire`,
        hireFormData,
        {
          headers: {
            Authorization: `Bearer ${token}`
          }
        }
      );
      
      setHiredInfo(response.data.employeeInfo);
      fetchApplications(); // Refresh the applications list
      toast.success('Employee account created successfully');
    } catch (error) {
      console.error('Error creating employee account:', error);
      toast.error(error.response?.data?.message || 'Failed to create employee account');
    } finally {
      setHiringLoading(false);
    }
  };

  return (
    <div className="applications-management">
      <div className="section-header">
        <h2>Job Applications</h2>
        <button 
          onClick={fetchApplications}
          className="refresh-btn"
          disabled={loading}
          style={{
            backgroundColor: '#007bff',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            padding: '6px 12px',
            cursor: 'pointer',
            display: 'inline-flex',
            alignItems: 'center',
            gap: '5px',
            fontSize: '0.9rem'
          }}
        >
          <i className="fas fa-sync-alt"></i> Refresh
        </button>
      </div>

      {loading ? (
        <div className="loading-spinner">
          <div className="spinner"></div>
        </div>
      ) : applications.length === 0 ? (
        <div className="no-data-message">
          <p>No job applications received yet.</p>
        </div>
      ) : (
        <div className="table-responsive">
          <table className="data-table" style={{ borderCollapse: 'collapse', width: '100%' }}>
            <thead>
              <tr style={{ backgroundColor: '#f8f9fa' }}>
                <th style={{ padding: '12px 15px', textAlign: 'left', borderBottom: '2px solid #e9ecef' }}>Name</th>
                <th style={{ padding: '12px 15px', textAlign: 'left', borderBottom: '2px solid #e9ecef' }}>Position</th>
                <th style={{ padding: '12px 15px', textAlign: 'left', borderBottom: '2px solid #e9ecef' }}>Applied On</th>
                <th style={{ padding: '12px 15px', textAlign: 'left', borderBottom: '2px solid #e9ecef' }}>Status</th>
                <th style={{ padding: '12px 15px', textAlign: 'left', borderBottom: '2px solid #e9ecef', minWidth: '220px' }}>Actions</th>
              </tr>
            </thead>
            <tbody>
              {applications.map((application) => (
                <tr key={application._id} className={application.status === 'Hired' ? 'row-hired' : ''} style={{ borderBottom: '1px solid #e9ecef' }}>
                  <td style={{ padding: '12px 15px' }}>{application.name}</td>
                  <td style={{ padding: '12px 15px' }}>{application.position}</td>
                  <td style={{ padding: '12px 15px' }}>{formatDate(application.applicationDate)}</td>
                  <td style={{ padding: '12px 15px' }}>
                    <span className={`status-badge ${application.status.toLowerCase()}`} style={{ padding: '4px 8px', borderRadius: '4px', fontSize: '0.85rem' }}>
                      {application.status}
                    </span>
                  </td>
                  <td style={{ padding: '12px 15px' }} className="actions-cell">
                    <div style={{ display: 'flex', gap: '8px', flexWrap: 'wrap' }}>
                      <button 
                        onClick={() => viewApplicationDetails(application)}
                        className="action-btn view-btn"
                        title="View Details"
                      >
                        <i className="fas fa-eye"></i> View
                      </button>
                      {application.status !== 'Hired' && application.status !== 'Rejected' && (
                        <>
                          <button 
                            onClick={() => handleAccept(application)}
                            className="action-btn accept-btn"
                            title="Accept"
                          >
                            <i className="fas fa-check"></i> Accept
                          </button>
                          <button 
                            onClick={() => handleReject(application._id)}
                            className="action-btn reject-btn"
                            title="Reject"
                          >
                            <i className="fas fa-times"></i> Reject
                          </button>
                        </>
                      )}
                      {application.resumePath && (
                        <button 
                          onClick={() => downloadResume(application._id)}
                          className="action-btn download-btn"
                          title="Download Resume"
                        >
                          <i className="fas fa-download"></i> Resume
                        </button>
                      )}
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}

      {/* Application Details Modal */}
      {showModal && selectedApplication && (
        <div className="modal-backdrop">
          <div className="modal-content large-modal">
            <div className="modal-header">
              <h3>Application Details</h3>
              <button 
                onClick={() => setShowModal(false)}
                className="close-btn"
              >
                <i className="fas fa-times"></i>
              </button>
            </div>
            <div className="modal-body">
              <div className="application-details">
                <h4>Personal Information</h4>
                <div className="detail-row">
                  <span className="detail-label">Name:</span>
                  <span className="detail-value">{selectedApplication.name}</span>
                </div>
                <div className="detail-row">
                  <span className="detail-label">Email:</span>
                  <span className="detail-value">{selectedApplication.email}</span>
                </div>
                <div className="detail-row">
                  <span className="detail-label">Phone:</span>
                  <span className="detail-value">{selectedApplication.phone}</span>
                </div>
                {selectedApplication.dateOfBirth && (
                  <div className="detail-row">
                    <span className="detail-label">Date of Birth:</span>
                    <span className="detail-value">{formatDate(selectedApplication.dateOfBirth)}</span>
                  </div>
                )}
                {selectedApplication.gender && (
                  <div className="detail-row">
                    <span className="detail-label">Gender:</span>
                    <span className="detail-value">{selectedApplication.gender}</span>
                  </div>
                )}
                {selectedApplication.address && (
                  <div className="detail-row">
                    <span className="detail-label">Address:</span>
                    <span className="detail-value">{selectedApplication.address}</span>
                  </div>
                )}
                {selectedApplication.emergencyContact && (
                  <div className="detail-row">
                    <span className="detail-label">Emergency Contact:</span>
                    <span className="detail-value">{selectedApplication.emergencyContact}</span>
                  </div>
                )}

                <h4>Professional Information</h4>
                <div className="detail-row">
                  <span className="detail-label">Position:</span>
                  <span className="detail-value">{selectedApplication.position}</span>
                </div>
                <div className="detail-row">
                  <span className="detail-label">Experience:</span>
                  <span className="detail-value">{selectedApplication.experience || 'N/A'}</span>
                </div>
                {selectedApplication.expectedSalary && (
                  <div className="detail-row">
                    <span className="detail-label">Expected Salary:</span>
                    <span className="detail-value">{selectedApplication.expectedSalary}</span>
                  </div>
                )}
                <div className="detail-row">
                  <span className="detail-label">Applied On:</span>
                  <span className="detail-value">{formatDate(selectedApplication.applicationDate)}</span>
                </div>
                <div className="detail-row">
                  <span className="detail-label">Status:</span>
                  <span className={`status-badge ${selectedApplication.status.toLowerCase()}`}>
                    {selectedApplication.status}
                  </span>
                </div>
                {selectedApplication.message && (
                  <div className="detail-row">
                    <span className="detail-label">Additional Information:</span>
                    <div className="detail-message">{selectedApplication.message}</div>
                  </div>
                )}
                
                {selectedApplication.username && (
                  <div className="detail-row">
                    <span className="detail-label">Username:</span>
                    <span className="detail-value">{selectedApplication.username}</span>
                  </div>
                )}

                <h4>Documents</h4>
                <div className="documents-grid">
                  {selectedApplication.resumePath && (
                    <div className="document-item">
                      <span className="document-label">Resume/CV</span>
                      <div className="document-action">
                        <button 
                          onClick={() => downloadResume(selectedApplication._id)}
                          className="btn btn-sm btn-primary"
                        >
                          <i className="fas fa-download"></i> Download
                        </button>
                      </div>
                    </div>
                  )}
                  
                  {selectedApplication.idProofPath && (
                    <div className="document-item">
                      <span className="document-label">ID Proof</span>
                      <div className="document-action">
                        <button 
                          onClick={() => downloadDocument(selectedApplication._id, 'idProof')}
                          className="btn btn-sm btn-primary"
                        >
                          <i className="fas fa-download"></i> Download
                        </button>
                      </div>
                    </div>
                  )}
                  
                  {selectedApplication.addressProofPath && (
                    <div className="document-item">
                      <span className="document-label">Address Proof</span>
                      <div className="document-action">
                        <button 
                          onClick={() => downloadDocument(selectedApplication._id, 'addressProof')}
                          className="btn btn-sm btn-primary"
                        >
                          <i className="fas fa-download"></i> Download
                        </button>
                      </div>
                    </div>
                  )}
                  
                  {selectedApplication.policeVerificationPath && (
                    <div className="document-item">
                      <span className="document-label">Police Verification</span>
                      <div className="document-action">
                        <button 
                          onClick={() => downloadDocument(selectedApplication._id, 'policeVerification')}
                          className="btn btn-sm btn-primary"
                        >
                          <i className="fas fa-download"></i> Download
                        </button>
                      </div>
                    </div>
                  )}
                  
                  {selectedApplication.photoPath && (
                    <div className="document-item">
                      <span className="document-label">Photo</span>
                      <div className="document-preview">
                        <img 
                          src={`http://localhost:5000/${selectedApplication.photoPath.replace(/\\/g, '/')}`}
                          alt="Applicant Photo"
                          className="document-image"
                        />
                      </div>
                    </div>
                  )}
                </div>
              </div>
            </div>
            <div className="modal-footer">
              {selectedApplication.status !== 'Hired' && selectedApplication.status !== 'Rejected' && (
                <>
                  <button 
                    onClick={() => {
                      setShowModal(false);
                      handleAccept(selectedApplication);
                    }}
                    className="btn btn-success"
                  >
                    Accept
                  </button>
                  <button 
                    onClick={() => {
                      handleReject(selectedApplication._id);
                      setShowModal(false);
                    }}
                    className="btn btn-danger"
                  >
                    Reject
                  </button>
                </>
              )}
              <button 
                onClick={() => setShowModal(false)}
                className="btn btn-secondary"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Accept Modal */}
      {showAcceptModal && selectedApplication && (
        <div className="modal-backdrop">
          <div className="modal-content hire-modal">
            <div className="modal-header">
              <h3>Accept Application</h3>
              <button 
                onClick={() => setShowAcceptModal(false)}
                className="close-btn"
              >
                <i className="fas fa-times"></i>
              </button>
            </div>
            <div className="modal-body">
              {hiredInfo ? (
                <div className="hired-info">
                  <h4>Employee Account Created</h4>
                  <div className="alert alert-success">
                    <p>Application accepted successfully. Employee account created with these credentials:</p>
                  </div>
                  <div className="credential-box">
                    <div className="credential-row">
                      <span className="credential-label">Username:</span>
                      <span className="credential-value">{hiredInfo.username}</span>
                    </div>
                    <div className="credential-row">
                      <span className="credential-label">Password:</span>
                      <span className="credential-value">{hiredInfo.password}</span>
                    </div>
                    <div className="credential-row">
                      <span className="credential-label">Role:</span>
                      <span className="credential-value">{hiredInfo.role}</span>
                    </div>
                  </div>
                  <div className="alert alert-warning">
                    <p><strong>Note:</strong> Please securely share these credentials with the employee. This is the only time the password will be shown.</p>
                  </div>
                </div>
              ) : (
                <form onSubmit={handleHireSubmit} className="hire-form">
                  <div className="form-group">
                    <label htmlFor="role">Role:</label>
                    <select 
                      id="role" 
                      name="role" 
                      value={hireFormData.role}
                      onChange={handleHireFormChange}
                      required
                    >
                      <option value="Worker">Worker</option>
                      <option value="Manager">Manager</option>
                      <option value="Admin">Admin</option>
                    </select>
                  </div>
                  <div className="form-group">
                    <label htmlFor="salary">Salary ():</label>
                    <input 
                      type="number" 
                      id="salary" 
                      name="salary" 
                      value={hireFormData.salary}
                      onChange={handleHireFormChange}
                      placeholder="Enter monthly salary"
                      min="1000"
                      required
                    />
                  </div>
                  <div className="alert alert-info">
                    <p>Accepting this application will create an employee account for {selectedApplication.name} with login credentials.</p>
                  </div>
                  <div className="form-actions">
                    <button 
                      type="button" 
                      className="btn btn-secondary"
                      onClick={() => setShowAcceptModal(false)}
                    >
                      Cancel
                    </button>
                    <button 
                      type="submit" 
                      className="btn btn-success"
                      disabled={hiringLoading}
                    >
                      {hiringLoading ? 'Processing...' : 'Accept & Create Account'}
                    </button>
                  </div>
                </form>
              )}
            </div>
            {hiredInfo && (
              <div className="modal-footer">
                <button 
                  onClick={() => setShowAcceptModal(false)} 
                  className="btn btn-primary"
                >
                  Close
                </button>
              </div>
            )}
          </div>
        </div>
      )}

      {/* Hire Modal (Legacy - can be removed if not needed) */}
      {showHireModal && selectedApplication && (
        <div className="modal-backdrop">
          <div className="modal-content hire-modal">
            <div className="modal-header">
              <h3>Hire Applicant</h3>
              <button 
                onClick={() => setShowHireModal(false)}
                className="close-btn"
              >
                <i className="fas fa-times"></i>
              </button>
            </div>
            <div className="modal-body">
              {hiredInfo ? (
                <div className="hired-info">
                  <h4>Employee Account Created</h4>
                  <div className="alert alert-success">
                    <p>Employee has been hired successfully. Please share these credentials with them:</p>
                  </div>
                  <div className="credential-box">
                    <div className="credential-row">
                      <span className="credential-label">Username:</span>
                      <span className="credential-value">{hiredInfo.username}</span>
                    </div>
                    <div className="credential-row">
                      <span className="credential-label">Password:</span>
                      <span className="credential-value">{hiredInfo.password}</span>
                    </div>
                    <div className="credential-row">
                      <span className="credential-label">Role:</span>
                      <span className="credential-value">{hiredInfo.role}</span>
                    </div>
                  </div>
                  <div className="alert alert-warning">
                    <p><strong>Note:</strong> Please securely share these credentials with the employee. This is the only time the password will be shown.</p>
                  </div>
                </div>
              ) : (
                <form onSubmit={handleHireSubmit} className="hire-form">
                  <div className="form-group">
                    <label htmlFor="role">Role:</label>
                    <select 
                      id="role" 
                      name="role" 
                      value={hireFormData.role}
                      onChange={handleHireFormChange}
                      required
                    >
                      <option value="Worker">Worker</option>
                      <option value="Manager">Manager</option>
                      <option value="Admin">Admin</option>
                    </select>
                  </div>
                  <div className="form-group">
                    <label htmlFor="salary">Salary ():</label>
                    <input 
                      type="number" 
                      id="salary" 
                      name="salary" 
                      value={hireFormData.salary}
                      onChange={handleHireFormChange}
                      placeholder="Enter monthly salary"
                      min="1000"
                      required
                    />
                  </div>
                  <div className="alert alert-info">
                    <p>This will create an employee account for {selectedApplication.name} and mark their application as 'Hired'.</p>
                  </div>
                  <div className="form-actions">
                    <button 
                      type="button" 
                      className="btn btn-secondary"
                      onClick={() => setShowHireModal(false)}
                    >
                      Cancel
                    </button>
                    <button 
                      type="submit" 
                      className="btn btn-primary"
                      disabled={hiringLoading}
                    >
                      {hiringLoading ? 'Processing...' : 'Hire Applicant'}
                    </button>
                  </div>
                </form>
              )}
            </div>
            {hiredInfo && (
              <div className="modal-footer">
                <button 
                  onClick={() => setShowHireModal(false)} 
                  className="btn btn-primary"
                >
                  Close
                </button>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default ApplicationsManagement; 