import { BrowserRouter as Router, Routes, Route, Navigate } from "react-router-dom";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import Login from "./pages/Login";
import AdminDashboard from "./pages/AdminDashboard";
import AddEmployee from "./pages/AddEmployee";
import EmployeePortal from "./pages/EmployeePortal";
import Registration from "./pages/Registration";
import { useAuth, AuthProvider } from "./contexts/AuthContext";
import TestStyles from "./test-styles";
import PureCSS from './PureCSS';
import JobApplication from './components/JobApplication';

function ProtectedRoute({ children, role }) {
  const { user } = useAuth();
  
  // Debug output to console
  console.log("Protected Route Check:", { user, requiredRole: role });
  
  if (!user) {
    console.log("No user found, redirecting to login");
    return <Navigate to="/" />;
  }
  
  const userRole = user.role?.toLowerCase() || "";
  
  if (role && userRole !== role.toLowerCase()) {
    console.log(`User role ${userRole} doesn't match required role ${role.toLowerCase()}`);
    return <Navigate to="/" />;
  }
  
  return children;
}

function AppRoutes() {
  return (
    <Routes>
      <Route path="/test-styles" element={<TestStyles />} />
      <Route path="/pure-css" element={<PureCSS />} />
      <Route path="/" element={<Login />} />
      <Route path="/register" element={<Registration />} />
      <Route path="/apply" element={<JobApplication />} />
      <Route
        path="/admin"
        element={
          <ProtectedRoute role="admin">
            <AdminDashboard />
          </ProtectedRoute>
        }
      />
      <Route
        path="/admin/add-employee"
        element={
          <ProtectedRoute role="admin">
            <AddEmployee />
          </ProtectedRoute>
        }
      />
      <Route
        path="/employee"
        element={
          <ProtectedRoute role="employee">
            <EmployeePortal />
          </ProtectedRoute>
        }
      />
    </Routes>
  );
}

export default function App() {
  return (
    <AuthProvider>
      <Router>
        <AppRoutes />
        <ToastContainer position="top-right" autoClose={3000} />
      </Router>
    </AuthProvider>
  );
} 